.gpt3__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 0.02em;
  color: #ffffff;
}

.gpt3__features-container__feature-title div {
  width: 38px;
  height: 3px;

  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0,0,0,0.25);
  margin-bottom: 1rem;
}


/* .gpt3__features-container_feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;
} */
.gpt3__features-container__feature-text p{
font-family:var(--font-family);
font-weight: 350;
font-size: 20px;
line-height: 30px;
color:var(--color-text)
}

@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .gpt3__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .gpt3__features-container__feature {
        margin: 1rem 0;
    }
}